.tabs__component_wzjlg {
    position: relative;
    display: inline-flex;
    min-width: 100%
}
.tabs__component_wzjlg:before {
        content: '';
        display: block;
        position: absolute;
        bottom: 1px;
        height: 1px;
        width: 100%;
        background-color: var(--primary-tablist-bottom-border-color);
    }
.tabs__fullWidthScroll_wzjlg {
    min-width: calc(100% - 2 * var(--gap-m));
    margin: 0 var(--gap-m);
}
.tabs__container_wzjlg {
    position: relative;
    overflow: hidden;
}
.tabs__title_wzjlg {
    display: flex;
    align-items: center;
    height: 100%;
    padding: 0;
    cursor: pointer;
    color: var(--primary-tablist-color);
    font-feature-settings: 'ss01';
    transition: color 0.2s ease;
    border: 0;
    background: none;
    white-space: nowrap;
    box-shadow: none;
    -webkit-user-select: none;
            user-select: none;
    cursor: pointer;
    outline: none
}
.tabs__title_wzjlg:not(.tabs__disabled_wzjlg):hover {
        color: var(--primary-tablist-hover-color);
    }
.tabs__focused_wzjlg {
    outline: 2px solid var(--focus-color);
    outline-offset: 2px;
}
.tabs__selected_wzjlg {
    cursor: default;
    color: var(--primary-tablist-selected-color);
}
.tabs__disabled_wzjlg {
    cursor: var(--disabled-cursor);
    color: var(--primary-tablist-disabled-color);
}
.tabs__line_wzjlg {
    position: absolute;
    height: 3px;
    bottom: 0;
    left: 0;
    background-color: var(--primary-tablist-line-color);
    transition: transform 0.2s ease, width 0.2s ease;
}
/* sizes */
.tabs__s_wzjlg .tabs__title_wzjlg, .tabs__xs_wzjlg .tabs__title_wzjlg, .tabs__xxs_wzjlg .tabs__title_wzjlg {
        padding: var(--primary-tablist-s-padding);
        font-size: var(--primary-tablist-s-font-size);
        font-weight: var(--primary-tablist-s-font-weight);
        font-family: var(--primary-tablist-s-font-family);
        line-height: 24px;
    }
.tabs__s_wzjlg .tabs__title_wzjlg + .tabs__title_wzjlg, .tabs__xs_wzjlg .tabs__title_wzjlg + .tabs__title_wzjlg, .tabs__xxs_wzjlg .tabs__title_wzjlg + .tabs__title_wzjlg {
        margin-left: var(--primary-tablist-s-gaps);
    }
.tabs__m_wzjlg .tabs__title_wzjlg {
        padding: var(--primary-tablist-m-padding);
        font-size: var(--primary-tablist-m-font-size);
        font-weight: var(--primary-tablist-m-font-weight);
        font-family: var(--primary-tablist-m-font-family);
        line-height: 24px;
    }
.tabs__m_wzjlg .tabs__title_wzjlg + .tabs__title_wzjlg {
        margin-left: var(--primary-tablist-m-gaps);
    }
.tabs__l_wzjlg .tabs__title_wzjlg {
        padding: var(--primary-tablist-l-padding);
        font-size: var(--primary-tablist-l-font-size);
        font-weight: var(--primary-tablist-l-font-weight);
        font-family: var(--primary-tablist-l-font-family);
        line-height: 24px;
    }
.tabs__l_wzjlg .tabs__title_wzjlg + .tabs__title_wzjlg {
        margin-left: var(--primary-tablist-l-gaps);
    }
.tabs__xl_wzjlg .tabs__title_wzjlg {
