/* hash: 10x51 */
:root {
    --color-static-status-green: #13a463;
    --color-static-status-grey: #6d7986;
    --color-static-status-orange: #de6a00;
    --color-static-status-red: #d91d0b;
}
:root {
    --color-light-bg-attention-muted: #fff3e0;
    --color-light-bg-negative-muted: #feebea;
    --color-light-bg-positive-muted: #ebf9f1;
    --color-light-bg-secondary: #f3f4f5;
    --color-light-border-link: #007aff;
    --color-light-text-primary: #0b1f35;
}
:root {

    /* Hard */

    /* Up */

    /* Hard up */
}
:root {
    --gap-3xs: 2px;
    --gap-2xs: 4px;
    --gap-xs: 8px;
    --gap-s: 12px;
    --gap-m: 16px;
    --gap-xl: 24px;
    --gap-2xs-neg: -4px;
}
:root {
    --border-radius-m: 8px;
}
:root {
    --focus-color: var(--color-light-border-link);
    --arrow-transform: rotate(180deg);
}
:root {
    /* border */
    --plate-border: 1px solid;
    --plate-border-radius: var(--border-radius-m);

    /* icons */
    --plate-closer-icon: url('https://alfabank.servicecdn.ru/icons/glyph_cross_m.svg');
    --plate-arrow-icon: url('https://alfabank.servicecdn.ru/icons/glyph_chevron-down_m.svg');
}
.plate__component_z3rop {
    position: relative;
    display: inline-flex;
    box-sizing: border-box;
    border-radius: var(--plate-border-radius);
    border: var(--plate-border);
    color: var(--color-light-text-primary);
    width: 100%;
    min-height: 56px;
    padding: var(--gap-m);
    outline: none;
}
.plate__foldable_z3rop {
    cursor: pointer;
}
.plate__component_z3rop * {
    box-sizing: border-box;
}
.plate__focused_z3rop {
    outline: 2px solid var(--focus-color);
    outline-offset: 2px;
}
.plate__inner_z3rop {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.plate__common_z3rop {
    background-color: var(--color-light-bg-secondary);
    border-color: var(--color-static-status-grey);
}
.plate__negative_z3rop {
    background-color: var(--color-light-bg-negative-muted);
    border-color: var(--color-static-status-red);
}
.plate__positive_z3rop {
    background-color: var(--color-light-bg-positive-muted);
    border-color: var(--color-static-status-green);
}
.plate__attention_z3rop {
    background-color: var(--color-light-bg-attention-muted);
    border-color: var(--color-static-status-orange);
}
.plate__isHidden_z3rop {
    display: none;
}
.plate__leftAddons_z3rop {
    flex-shrink: 0;
    min-width: 24px;
    margin-right: var(--gap-s);
}
.plate__title_z3rop {
    font-size: 18px;
    line-height: 24px;
    font-weight: 700;

    word-break: break-word;
}
.plate__content_z3rop {
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;

    max-height: 300vh;
    overflow: hidden;
    transition: max-height 2s ease-in-out;
    padding: var(--gap-2xs);
    margin: var(--gap-2xs-neg);
    cursor: default
}
.plate__content_z3rop.plate__isFolded_z3rop {
        max-height: 0;
        transition: max-height 1s cubic-bezier(0, 1, 0, 1);
    }
.plate__contentContainer_z3rop {
    flex: 1;
    max-width: 586px;
    margin-right: auto
}
.plate__contentContainer_z3rop.plate__withoutTitle_z3rop {
        padding-top: var(--gap-3xs);
    }
.plate__title_z3rop + .plate__content_z3rop > .plate__contentInner_z3rop {
    padding-top: var(--gap-xs);
}
.plate__closer_z3rop,
.plate__folder_z3rop {
    display: block;
    height: 24px;
    width: 24px;
    opacity: 1;
    transition: opacity 0.2s ease;
    cursor: pointer;
    margin-left: var(--gap-m)
}
.plate__closer_z3rop:hover, .plate__folder_z3rop:hover {
        opacity: 0.7;
    }
.plate__closer_z3rop:active, .plate__folder_z3rop:active {
        opacity: 0.5;
    }
.plate__closer_z3rop {
    background: var(--plate-closer-icon) center no-repeat;
}
.plate__folder_z3rop {
    background: var(--plate-arrow-icon) center no-repeat;
    transition: transform 0.15s ease-in-out, opacity 0.2s ease;
    transform: var(--arrow-transform)
}
.plate__folder_z3rop.plate__isFolded_z3rop {
        transform: rotate(0deg);
    }
.plate__buttons_z3rop {
    margin-top: var(--gap-xl);
}
.plate__button_z3rop {
    margin-right: var(--gap-s)
}
.plate__button_z3rop:last-child {
        margin-right: 0;
    }
