.side-panel__appear_shto1,
.side-panel__enter_shto1 {
    opacity: 0;
    transform: translateY(15px);
}

.side-panel__appearActive_shto1,
.side-panel__enterActive_shto1 {
    opacity: 1;
    transform: translateY(0);
    transition: opacity 200ms ease-in, transform 200ms ease-in;
}

.side-panel__exit_shto1 {
    opacity: 1;
    transform: translateY(0);
}

.side-panel__exitActive_shto1,
.side-panel__exitDone_shto1 {
    opacity: 0;
    transform: translateY(15px);
    transition: opacity 200ms ease-out, transform 200ms ease-out;
}
