/* hash: h99us */
.side-panel__appear_ol8op,
.side-panel__enter_ol8op {
    opacity: 0;
    transform: translateX(15px);
}

.side-panel__appearActive_ol8op,
.side-panel__enterActive_ol8op {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 200ms ease-in, transform 200ms ease-in;
}

.side-panel__exit_ol8op {
    opacity: 1;
    transform: translateX(0);
}

.side-panel__exitActive_ol8op,
.side-panel__exitDone_ol8op {
    opacity: 0;
    transition: opacity 200ms ease-out, transform 200ms ease-out;
    transform: translateX(15px);
}
